// @ is an alias to /src
import { Component, Vue } from "vue-property-decorator";
import { DashBoardCard } from '@/model/DashBoardCard';
import { firestore } from '@/firebaseInit';
import  UserList  from '@/components/userList/UserList.vue';

@Component({
  components: {
    UserList
  }
})
export default class SuperAdmin extends Vue {
  dashboardDetails: DashBoardCard[] = []

  private loading = true;
  async mounted() {
    try {
      const date = new Date();
      const currYear = date.getFullYear().toString();
      const currMonth = date.getMonth();
      this.buildDefaultDashboard(currYear, currMonth)
      const yearData = await firestore.collection('analytics').doc(currYear).get();
      if (yearData.exists) {
        this.fillYearData(yearData.data());
        const monthData = await firestore.collection('analytics').doc(currYear).collection('months').doc(currMonth.toString()).get();
        if (monthData.exists) { 
this.fillmonthData(monthData.data());
        }
      }
    } catch (error) {

    }
  }
  fillYearData(data: any) {
    this.dashboardDetails[0].value=data?.sale?this.formatNumber(data.sale):this.formatNumber(0);
    this.dashboardDetails[1].value=data?.expense?this.formatNumber(data.expense):this.formatNumber(0);
  }
  fillmonthData(data: any) {
    this.dashboardDetails[2].value=data?.sale?this.formatNumber(data.sale):this.formatNumber(0);
    this.dashboardDetails[3].value=data?.expense?this.formatNumber(data.expense):this.formatNumber(0);
  }
  buildDefaultDashboard(currYear: string, currMonth: number) {
    const yearSaleCard = new DashBoardCard(currYear, 'Overall Sales', this.formatNumber(0), 'green');
    const yearExpenseCard = new DashBoardCard(currYear, 'Overall Expenses', this.formatNumber(0), 'red');
    const monthSaleCard = new DashBoardCard(this.getMonth(currMonth) + "-" + currYear, 'Month Sales', this.formatNumber(0), 'green');
    const monthExpenseCard = new DashBoardCard(this.getMonth(currMonth) + "-" + currYear, 'Month Expenses', this.formatNumber(0), 'red');
    this.dashboardDetails.push(yearSaleCard);
    this.dashboardDetails.push(yearExpenseCard);
    this.dashboardDetails.push(monthSaleCard);
    this.dashboardDetails.push(monthExpenseCard);
  }
  getMonth(index: number) {
    const monthNames = ["Jan", "Feb", "Mar", "Apr",
      "May", "Jun", "Jul", "Aug",
      "Sep", "Oct", "Nov", "Dec"];
    return monthNames[index];
  }
  formatNumber(number: any, attachCurrency = true) {
    number = number.toString()
    let afterPoint = ''
    if (number.indexOf('.') > 0) {
      afterPoint = number.substring(number.indexOf('.'), number.length)
    }
    number = Math.floor(number)
    number = number.toString()
    let lastThree = number.substring(number.length - 3)
    const otherNumbers = number.substring(0, number.length - 3)
    if (otherNumbers !== '') {
      lastThree = ',' + lastThree
    }
    const res = otherNumbers.replace(/\B(?=(\d{2})+(?!\d))/g, ',') + lastThree + afterPoint

    return attachCurrency ? `â‚¹ ${res}` : res
  }
}
